"use client";

import { useEffect, useState } from "react";
import { collection, getDocs, query } from "firebase/firestore";
import { db } from "@/lib/firebase";

interface Movement {
    id: string;
    title: string;
    type?: string;
    artist?: string;
    date?: string;
    description?: string;
}

export default function MovementsPage() {
    const [movements, setMovements] = useState<Movement[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchMovements = async () => {
            try {
                const q = query(collection(db, "movements"));
                const querySnapshot = await getDocs(q);
                const data = querySnapshot.docs.map((doc) => ({
                    id: doc.id,
                    ...doc.data(),
                })) as Movement[];
                setMovements(data);
            } catch (error) {
                console.error("Error fetching movements:", error);
            } finally {
                setLoading(false);
            }
        };

        fetchMovements();
    }, []);

    return (
        <div className="flex flex-col gap-12">
            <h1 className="text-4xl md:text-7xl font-black uppercase tracking-tighter">
                Current <span className="text-stroke-white text-transparent md:text-white">Movements</span>
            </h1>

            <div className="border-t border-white">
                {loading ? (
                    <div className="py-12 text-center text-zinc-500 font-mono">Loading archive...</div>
                ) : movements.length === 0 ? (
                    <div className="py-12 text-center text-zinc-500 font-mono">No movements archived yet.</div>
                ) : (
                    movements.map((move, index) => (
                        <div
                            key={move.id}
                            className="group flex flex-col md:flex-row md:items-center justify-between py-6 border-b border-white hover:bg-white hover:text-black transition-colors duration-200 cursor-pointer px-2"
                        >
                            <div className="flex items-baseline gap-4">
                                <span className="font-mono text-xs md:text-sm w-8">0{index + 1}</span>
                                <h2 className="text-2xl md:text-4xl font-bold uppercase group-hover:italic transition-all">
                                    {move.title}
                                </h2>
                            </div>

                            <div className="flex items-center gap-4 md:gap-12 mt-2 md:mt-0 pl-12 md:pl-0 font-mono text-xs md:text-sm uppercase">
                                {/* Fallback for type/artist if not present in schema yet - assuming Description or custom text might be used */}
                                <span className="bg-white text-black px-2 py-0.5 rounded-full group-hover:bg-black group-hover:text-white transition-colors">
                                    {move.type || "Movement"}
                                </span>
                                <span className="w-32 text-right truncate">{move.artist || "Nava Fundacion"}</span>
                            </div>
                        </div>
                    ))
                )}
            </div>
        </div>
    );
}
